//PATHWAY GOTHIC ONE -- only has one font-weight
@font-face {
	font-family: 'Pathway Gothic One', sans-serif;
	src: url('../fonts/PathwayGothicOne-Regular.ttf')  format('truetype');
}

@import url("http://fonts.googleapis.com/css?family=Pathway+Gothic+One");

@pathway-font-family: 'Pathway Gothic One', sans-serif;

.pathway-font {
	font-family: @pathway-font-family;
}


//LOBSTER FONT -- only has one font-weight
